* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #252525;
  color: #e7e713;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: 778px) {
  .container {
    width: 750px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}
.m-navbar {
  background-color: #eee;
  padding: 10px;
}
.m-navbar .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.m-navbar .container .m-brand {
  height: 100%;
  width: 100px;
  border-radius: 12px;
}
.m-navbar .container .m-brand .m-brand-img {
  width: 100%;
  height: 100%;
  border-radius: 12px;
}
.m-navbar .container .m-nav-links {
  position: relative;
}
.m-navbar .container .m-nav-links ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.m-navbar .container .m-nav-links ul .m-active {
  border-bottom: 1px solid blue;
}
.m-navbar .container .m-nav-links ul li {
  padding-left: 20px;
  display: inline-block;
}
.m-navbar .container .m-nav-links ul li a {
  font-size: 26px;
  text-decoration: none;
  padding: 10px;
  color: #e7e713;
  transition: 0.3;
  background-color: #252525;
}

header {
  margin-top: 20px;
}
header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .container img {
  width: 70%;
  height: auto;
  border-radius: 50%;
}